<template>
  <section class="card p-4 rounded-0 my-3">
    <h4>Text Editor</h4>
    <div id="editor" style="min-height: 400px"></div>
    <button class="btn btn-primary" @click="onClick">Run</button>
  </section>
</template>

<script>
import loader from "@monaco-editor/loader";

export default {
  name: "Text-Editor",
  async mounted() {
    loader.init().then((monaco) => {
      monaco.editor.create(document.getElementById("editor"));
    });
  },
  data() {
    return {
      text: "",
    };
  },
  methods: {
    onClick() {
      console.log(document.getElementById("editor").textContent);
    },
  },
};
</script>