<template>
  <section class="card p-4 rounded-0">
    <h4>Charts</h4>
    <VueApexCharts
      type="line"
      :options="options"
      :series="dataSeries"
    ></VueApexCharts>
  </section>
</template>

<script>
import VueApexCharts from "vue-apexcharts";

export default {
  name: "Metrics-Chart",
  components: {
    VueApexCharts,
  },
  props: {
    dataSeries: Array,
  },
  data() {
    return {
      options: {},
    };
  },
  methods: {},
  created() {
    this.options = {
      grid: {
        borderColor: "#f1f1f1",
      },
      column: {
        borderColor: "#ebebee",
      },
      xaxis: {
        categories: [
          "Jan",
          "Feb",
          "Mar",
          "Apr",
          "May",
          "Jun",
          "Jul",
          "Aug",
          "Sep",
          "Oct",
          "Nov",
          "Dec",
        ],
      },
    };
  },
};
</script>