<template>
  <div id="app">
    <Header appName="Ashipa Electric Company"></Header>

    <div class="container">
      <div class="row" id="location">
        <div class="col-sm-8">
          <Location />
        </div>
        <div class="col-sm-4">
          <Weather />
        </div>
      </div>
      <div class="my-4" id="data-source">
        <DataSources :dataSources="dataSources" @get-metrics="getMetrics" />
      </div>

      <div class="row flex-column-md-reverse">
        <div class="col-sm-8">
          <Charts :dataSeries="metricsDataSeries" />
        </div>
        <div class="col-sm-4">
          <Metrics :metrics="filteredMetrics" />
        </div>
      </div>

      <div id="editor">
        <Editor />
      </div>
    </div>
  </div>
</template>

<script>
import Header from "./components/Header";
import Location from "./components/Location";
import DataSources from "./components/DataSources";
import Weather from "./components/Weather";
import Charts from "./components/Charts";
import Metrics from "./components/Metrics";
import Editor from "./components/Editor";

export default {
  name: "App",
  components: {
    Header,
    Location,
    Weather,
    DataSources,
    Charts,
    Metrics,
    Editor,
  },
  data() {
    return {
      dataSources: [],
      metrics: [],
      filteredMetrics: [],
      metricsDataSeries: [],
    };
  },
  methods: {
    getMetrics(dataSource) {
      this.filteredMetrics = this.metrics.filter(
        (item) => item.type === dataSource
      );

      let metricsData = this.filteredMetrics.map((metric) => {
        let data = [];
        for (let i = 0; i < 10; i++) {
          data.push(Math.floor(Math.random() * 100));
        }

        return {
          name: metric.name,
          data,
        };
      });

      this.metricsDataSeries = metricsData;
    },
  },
  created() {
    this.dataSources = [
      {
        id: "asset-1",
        name: "Asset 1",
      },
      {
        id: "asset-2",
        name: "Asset 2",
      },
    ];

    this.metrics = [
      {
        type: "asset-1",
        name: "Current",
      },
      {
        type: "asset-1",
        name: "Voltage",
      },
      {
        type: "asset-2",
        name: "Production",
      },
      {
        type: "asset-2",
        name: "Phase Voltage 1",
      },
      {
        type: "asset-1",
        name: "Nuetral Current",
      },
      {
        type: "asset-2",
        name: "Phase Voltage 2",
      },
      {
        type: "asset-2",
        name: "Voltage",
      },
      {
        type: "asset-1",
        name: "Power",
      },
    ];
  },
};
</script>

