<template>
  <div class="card p-4">
    <h4 class="">Todays Weather</h4>
    <img
      src="../assets/cloud.png"
      alt=""
      class="img-fluid col-7 d-block mx-auto"
    />
    <p class="text-center">
      <span style="font-size: 60px"> {{ temp }} </span>C
    </p>
  </div>
</template>

<script>
export default {
  name: "Weather-Widget",
  data() {
    return {
      temp: 0,
    };
  },
  methods: {},
  created() {
    async function getWeather() {
      const weather = await fetch(
        "https://data.api.xweather.com/forecasts/9.6825,4.6133?format=json&filter=day&limit=7&client_id=Zte40OMnm1gLJtDnZmT9k&client_secret=VQIOwf5eHIcvOcjyTLa9WzwLVZjfGZjnA3PKK6JF"
      );
      const response = await weather.json();
      if (response.success) {
        //    console.log(response.response[0].periods[0].minTempC);
        return response.response[0].periods[0].minTempC;
      }
    }

    getWeather().then((res) => (this.temp = res));
  },
};
</script>